import React, {Component} from 'react';
import './index.less';
export  default class CatalogComponent extends Component {
    constructor(){
        super();
        this.state={
            titleNum:0
        }
    }
    render() {
        console.log(this.props);
        return (
            <div className="catalog-component">
                {
                    this.props.data.data?this.props.data.data.map((item,index)=>(
                        <div key={index}>
                            <h3>{item.title}</h3>
                            {
                                item.upContent.map((item,index)=>(
                                    <h4 key={index}>{item.subTitle}</h4>
                                ))
                            }
                        </div>
                    )):<div>正在加载</div>
                }
            </div>
        )
    }
}



