import React, {Component} from 'react';
import CatalogComponent from '../CatalogComponent';
import JsListComponent from '../JsListComponent';
import './index.less';
export  default class JsDetailComponent extends Component {
    render() {
        let data=this.props.data;
        let titleNum=this.props.titleNum;
        return (
            <div className="js-detail">
                <JsListComponent data={this.props.data}/>
                <CatalogComponent data={data} titleNum={titleNum}/>
            </div>
        )
    }
}



