import React, {Component} from 'react';
import './index.less';
export  default class JsTextComponent extends Component {
    render() {
        return (
            <div className="js-text">
                <h3>{this.props.data.title}</h3>
                {
                    this.props.data.upContent?this.props.data.upContent.map((item,index)=>(
                        <div key={index}>
                            <h4>{item.subTitle}</h4>
                            {
                                item.content.map((item,index)=>(
                                    <p key={index}>{item}</p>
                                ))
                            }
                            <div className="demo">
                                {
                                    item.demo.map((item,index)=>(
                                        <p key={index}>{item}</p>
                                    ))
                                }
                            </div>

                        </div>
                    )):<div>正在加载</div>
                }
            </div>
        )
    }
}



