import React, {Component} from 'react';
import JsComponent from '../../../components/JsComponent';
import {getJs} from '../../../fetch/js/js';
export  default class Content extends Component {
    constructor(){
        super();
        this.state={
            data:{}
        }
    }
    componentDidMount(){
        getJs().then((res)=>res.json()).then(data=>{
            this.setState({
                data
            })
        })
    }
    render() {
        return (
            <div>
                <JsComponent data={this.state.data}/>
            </div>
        )
    }
}


