import React, {Component} from 'react';
import JsTextComponent from '../JsTextComponent';
import './index.less';
export  default class JsListComponent extends Component {
    render() {
        return (
            <div className="js-list">
                <h2>{this.props.data.h2}</h2>
                {
                    this.props.data.data?this.props.data.data.map((item, index) => (
                    <JsTextComponent data={item} key={index}/>
                    )):<div>正在加载</div>
                }
                <div>
                    <span>{this.props.data.date}</span><span>{this.props.data.name}</span><span>{this.props.data.number}</span>
                </div>
            </div>
        )
    }
}



