import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import './index.less';
export  default class DirectoryComponent extends Component {
    render() {
        let {h2,abstract,date,name,number,id}={...this.props.data};
        return (
            <div className="directory-component">
                <div className="title">
                    <Link to='/'>{h2}</Link>
                </div>
                <div className="abstract">
                    {abstract}
                    <Link to={'/'+this.props.type+'/'+id}>阅读全文</Link></div>
                <div className="content-info">
                    <span className="date">{date}</span>
                    <span className="name">作者:{name}</span>
                    <span className="number">阅读({number})</span>
                </div>
            </div>
        )
    }
}



