import React, {Component} from 'react';
import JsDetailComponent from '../../../components/JsDetailComponent';
import {getJs} from '../../../fetch/js/js';
export  default class List extends Component {
    constructor(){
        super();
        this.state={
            data:{},
            titleNum:0
        }
    }
    componentDidMount(){
        getJs().then((res)=>res.json()).then(data=>{
            this.setState({
                data,
                titleNum:data.data.length
            })
        })
    }
    render() {
        return (
            <div>
                <JsDetailComponent data={this.state.data} titleNum={this.state.titleNum}/>
            </div>
        )
    }
}



